import React from 'react';

const Contact = () => {
  return (
    <section id="contact" className="contact container">
      <h1 className="contact-heading">Contactez-moi</h1>
      <p className="contact-desc">Section à compléter avec votre formulaire ou vos infos de contact.</p>
    </section>
  );
};

export default Contact;
